.asset-spreadsheet {
  display: flex;
  flex-direction: column;
  border: 2px solid #888;
  border-radius: 10px;
  margin-top: 15px;
  text-align: center;
}

.asset-spreadsheet-header {
  display: flex;
  flex-wrap: wrap;
  padding: 5px 10px;
  border-bottom: 2px solid #888;
  font-weight: 600;
  font-size: 1.2em;
}

.no-assets-notice {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
