.login-form {
    max-width: 400px;
    width: 100%;
    margin: 0 auto;
}

.register-form {
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
}

.login-form label, .register-form label {
    margin-top: 10px;
}

.login-form input[type='checkbox'] {
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    background: white;
    border-radius: 5px;
    border: 2px solid #555;
}

input[type='checkbox']:checked {
    background: lightBlue;
}

.login-page {
    margin-top: 100px;
}

.error-message {
    margin: 10px;
    opacity: 0.75;
    color: red;
    font-size: small;
}


